import Titling from "../home/titling"
import info from './info.js'
import PharmacyCards from "./pharmacyCard"

const Manage = () => (
    <main>
        
        <Titling
            title={info.title}
            subtitle={info.subtitle}
            className=""
        />

        <PharmacyCards services={info.services} className="mt-4 max-w-6xl mx-auto" />
        
    </main>
)

export default Manage

