import { Link } from "preact-router/match"
import Card from "../../components/general/card"
import LinkCard from "../../components/general/linkCard"
import { MyLink } from "../../components/navigation/myLink"

const PharmacyCards = ({ className, services }) => (
    <div className={`${className} flex md:flex-row flex-col justify-around items-center flex-wrap`}>
        {services.map((service, index) => (
            <PharmacyCard
                title={service.title}
                description={service.description}
                url={service.url}
                image={service.image}
            ></PharmacyCard>
        ))}
    </div>
)

const PharmacyCard = ({ title, description, url, image }) => (
    <Card url={url} className="max-w-xs m-4" innerClassName="text-center p-4">
        
        <img src={image} className="rounded-md mb-4 aspect-video object-cover" />

        {/* <div className="font-bold text-2xl">
            {title}
        </div> */}

        <MyLink href={url}>
            <button className="bg-yellow-300 hover:bg-yellow-400 transition-all hover:py-4 rounded-xl font-bold text-xl py-2 px-3 w-full">
                {title}
            </button>
        </MyLink>

        {description &&
            <div className="mt-4 text-gray-400">
                {description}
            </div>
        }

    </Card>
)

export default PharmacyCards

